<template>
    <div>
        <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome'}">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: pathId }">我的客户</el-breadcrumb-item>
          <el-breadcrumb-item>客户详情</el-breadcrumb-item>
        </el-breadcrumb>
        <!-- 卡片视图区 -->
        <el-card class="box-card">
          <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>客户详情</span>
              <el-button style="float: right; padding: 3px 0" type="text" icon="el-icon-d-arrow-left" @click="returnHandle">返回</el-button>
            </div>
        <!-- 卡片内容 表单区 -->
            <el-form :model="addCustomRuleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
               <el-form-item label="客户状态" prop="CustomerStatusId">
                <el-select v-model="addCustomRuleForm.CustomerStatusId" placeholder="请选择客户状态">
                  <!-- <el-option label="跟进中" value="1"></el-option>
                  <el-option label="已成交" value="2"></el-option>
                  <el-option label="已失效" value="3"></el-option> -->
                  <el-option v-for="(item,index) in customerStatus" :key="index" :label="item.customerStatusName" :value="item.customerStatusId"></el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="客户名称" prop="CustomerName">
                <el-input v-model="addCustomRuleForm.CustomerName" class="el-input-width"></el-input>
              </el-form-item>
              <el-form-item label="联系人" prop="contactPersonName">
                <el-input v-model="addCustomRuleForm.contactPersonName" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="客户电话" prop="contactPersonPhone">
                <el-input v-model="addCustomRuleForm.contactPersonPhone" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="客户微信" prop="contactPersonWechat">
                <el-input v-model="addCustomRuleForm.contactPersonWechat" class="el-input-width"></el-input>
              </el-form-item>
               <el-form-item label="阿里旺旺号" prop="contactPersonWangwang">
                <el-input v-model="addCustomRuleForm.contactPersonWangwang" class="el-input-width"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="addCustomSubmitForm('ruleForm')" plain>立即新增</el-button>
                <el-button @click="addCustomResetForm('ruleForm')" plain type="danger">重置</el-button>
              </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      // 新增表单信息数组
      addCustomRuleForm: {
        CustomerStatusId: '',
        CustomerName: '',
        contactPersonName: '',
        contactPersonPhone: '',
        contactPersonWechat: '',
        contactPersonWangwang: '',
        UserId: '',
        UserName: ''
        // CustomerStatusName: ''
      },
      rules: {
        CustomerStatusName: [
          { required: true, message: '请选择客户状态', trigger: 'change' }
        ],
        CustomerName: [
          { required: true, message: '请填写客户名称', trigger: 'blur' }
        ],
        contactPersonName: [
          { required: true, message: '请填写联系人名称', trigger: 'blur' }
        ]
      },
      pathId: '',
      customerStatus: []
    }
  },
  created () {
    this.addCustomRuleForm.UserId = window.sessionStorage.getItem('userId')
    this.pathId = '/my/myclient'
    this.getUserInfo()
    this.getCustomerStatus()
  },
  methods: {
    // 获取客户状态
    async getCustomerStatus () {
      const { data: res } = await this.$http.post('/customerStatus/findCustomerStatus')
      this.customerStatus = res.data
    },
    // 获取用户信息
    async  getUserInfo () {
      const data = Qs.stringify({ userId: this.addCustomRuleForm.UserId })
      const { data: res } = await this.$http.post('/user/findUser', data)
      this.addCustomRuleForm.UserName = res.data.userName
    },
    addCustomSubmitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let flag = this.addCustomRuleForm.contactPersonPhone.trim() || this.addCustomRuleForm.contactPersonWechat.trim() || this.addCustomRuleForm.contactPersonWangwang.trim()
          if (!flag) return this.$message.error('至少输入一种联系方式！')
          // 验证手机号的规则
          // const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
          // const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|16[6]|19[89]]|17[01345678]|18[0-9]|14[579])[0-9]{8}$/
          const regMobile = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/
          if (this.addCustomRuleForm.contactPersonPhone.trim()) {
            if (regMobile.test(this.addCustomRuleForm.contactPersonPhone.trim())) {
              // console.log(this.addCustomRuleForm)
              // this.isCustomerStatusName(this.addCustomRuleForm.CustomerStatusId)
              // console.log(this.addCustomRuleForm)
              // 调用新增客户接口
              this.addCustomer()
            } else {
              return this.$message.error('请输入合法得手机号！')
            }
          } else {
            // 调用新增客户接口
            this.addCustomer()
          }
        } else {
          return this.$message.error('提交失败！')
        }
      })
    },
    // 新增客户接口调用
    async addCustomer () {
      this.isCustomerStatusName(this.addCustomRuleForm.CustomerStatusId - 0)

      // 调用新增客户接口

      const data = Qs.stringify(this.addCustomRuleForm)
      const { data: res } = await this.$http.post('/customer/addCustomer', data)
      this.addCustomResetForm('ruleForm')
      this.$router.push('/my/myclient')
      if (res.status !== 200) {
        return this.$message.error(res.msg)
      } else {
        this.$message.success(res.msg)
        this.$router.push(this.pathId)
      }
    },
    addCustomResetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 点击返回按钮触发该事件，返回上一页
    returnHandle () {
      this.$router.push(this.pathId)
    },
    // 判断客户状态名称
    isCustomerStatusName (CustomerStatusName) {
      if (CustomerStatusName === 1) {
        this.addCustomRuleForm.CustomerStatusName = '跟进中'
      } else if (CustomerStatusName === 2) {
        this.addCustomRuleForm.CustomerStatusName = '已成交'
      } else {
        this.addCustomRuleForm.CustomerStatusName = '已失效'
      }
    }
  }
}
</script>
<style lang="less" scoped>
// 表单输入框宽度
@el-input-width:500px;
.el-input-width{
    width: @el-input-width!important;
}
</style>
