<template>
    <div>
      <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>我的客户</el-breadcrumb-item>
        </el-breadcrumb>
      <!-- 卡片视图区 -->
        <el-card class="box-card">
          <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>我的客户</span>
            </div>

          <!-- 卡片内容区 -->
            <div>
            <!-- 卡片内容区头部 -->
              <el-row>
                <el-col :span="3">
                     <el-button type="primary" icon="el-icon-circle-plus-outline" size="middle" plain @click="createClientHandle">新增客户</el-button>
                </el-col>
                <el-col :span="6">
                  <el-input placeholder="请输入内容" v-model="searchValue" clearable @change="searchChange" @clear="searchClear()" class="input-with-select">
                    <el-button slot="append" icon="el-icon-search" @click="searchHandle"></el-button>
                  </el-input>
                </el-col>
              </el-row>
            <!-- 卡片内容表单区 -->
              <el-row>
                <el-table  :data="customerTableData" style="width: 100%" stripe border >
                  <el-table-column  type="index" width="50"> </el-table-column>
                  <el-table-column    prop="userName"   label="归属同事" width="90"  > </el-table-column>
                  <el-table-column  prop="customerUpdatedDate"    label="上次更进时间" width="150"  >  </el-table-column>
                  <el-table-column    prop="customerName"   label="客户名称" width="120"  > </el-table-column>
                  <el-table-column    prop="contactPersonName"   label="联系人" width="100"  > </el-table-column>
                  <el-table-column  prop="customerStatusId"  label="客户状态"  width="110"  >
                    <template slot-scope="scope">
                      <el-tag type="danger" v-if="scope.row.customerStatusId==='1'">跟进中</el-tag>
                      <el-tag type="success" v-else-if="scope.row.customerStatusId==='2'">已成交</el-tag>
                      <el-tag type="info" v-else>已失效</el-tag>
                    </template>
                  </el-table-column>
                  <el-table-column   prop="contactPersonPhone"   label="客户电话" width="120" >
                    <template slot-scope="scope">
                      {{scope.row.contactPersonPhone?scope.row.contactPersonPhone:'---'}}
                    </template>
                  </el-table-column>
                  <!-- 表格操作栏 -->
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button type="primary" size="mini" plain @click="clientDetailHandle(scope.row.customerId)">详情</el-button>
                      <el-button type="warning" size="mini" plain @click="clientUpdateHandle(scope.row.customerId,scope.row.customerName,scope.row.userId,scope.row.userName)">跟进</el-button>
                      <el-button type="success" size="mini" plain @click="linkmanHandle(scope.row.customerId)">联系人</el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-row>
          <!-- 卡片内容分页区 -->
             <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalNum">
             </el-pagination>
        </div>
      </el-card>

    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      // 客户表单数据
      customerTableData: [],
      // 控制点击详情按钮后对话框的显示和隐藏
      detailDialogVisible: false,
      // 控制点击修改按钮后对话框的显示和隐藏
      editDialogVisible: false,
      // 控制点击客户签单按钮后弹出对话框
      signBillDialogVisible: false,
      // 输入框value值
      searchInputValue: '',
      // 当前用户id
      // userid: '',
      // 总数据条数
      totalNum: null,
      // 页面数据条数
      pageSize: null,
      // 当前页数
      currentPage: 1,
      searchValue: ''
    }
  },
  created () {
    this.userid = window.sessionStorage.getItem('userId')
    // 获取当前员工拥有顾客
    this.getCustomer()
  },
  mounted () {

  },
  methods: {
    searchClear () {
      this.searchHandle()
    },
    searchChange () {
      this.searchHandle()
    },
    // 搜索
    async searchHandle () {
      const data = Qs.stringify({ userId: this.userid, customerSearchContent: this.searchValue.trim() })
      const { data: res } = await this.$http.post('/customer/findCustomerByUserId', data)
      if (res.status !== 200) return this.$message.error(res.msg)
      this.totalNum = res.data.totalNum - 0
      this.pageSize = res.data.pageSize - 0
      this.currentPage = res.data.currentPage
      this.customerTableData = res.data.items
    },
    handleCurrentChange (newPage) {
      this.currentPage = newPage
      this.getCustomer()
    },
    handleSizeChange (newSize) {
      this.pageSize = newSize
      this.getCustomer()
    },
    // 获取当前员工拥有顾客
    async getCustomer () {
      // this.$message.error('失败！')
      const data = Qs.stringify({ userId: this.userid, pageSize: this.pageSize, pageNum: this.currentPage })
      const { data: res } = await this.$http.post('/customer/findCustomerByUserId', data)
      if (res.status !== 200) return this.$message.error(res.msg)
      console.log(res)

      this.totalNum = res.data.totalNum - 0
      this.pageSize = res.data.pageSize - 0
      this.currentPage = res.data.currentPage
      this.customerTableData = res.data.items
    },
    // 点击新增客户按钮触发该事件
    createClientHandle (e) {
      // console.log(e)
      this.$router.push(`/my/createclient${this.userid}`)
    },
    // 点击详情按钮触发该事件
    clientDetailHandle (id) {
      // console.log('a')
      this.$router.push('/detail/customerDetail' + id)
    },
    // 点击跟进按钮后触发该事件
    clientUpdateHandle (customerId, customerName, userId, userName) {
      this.$router.push({ path: '/update/customerUpdate', query: { customerId: customerId, customerName: customerName, userId: userId, userName: userName } })
    },
    // 点击联系人按钮触发该事件
    linkmanHandle (id) {
      this.$router.push('/linkman/linkmanMain' + id)
    }
  },
  watch: {
    searchValue () {
      if (this.searchValue.trim().length <= 0) {
        this.getCustomer()
      }
    }
  }
}
</script>
<style lang="less" scoped>

</style>
