<template>
    <div>
         <!-- 面包屑导航 -->
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/welcome' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>客户公海</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
          <!-- 卡片头部 -->
            <div slot="header" class="clearfix">
              <span>客户公海</span>
            </div>
          <!-- 卡片内容区 -->
          <!-- 卡片内容表单区 -->
              <el-row>
                <el-table  :data="customerPubliceAreaList" style="width: 100%" stripe border >
                  <el-table-column  type="index" width="90"> </el-table-column>
                  <el-table-column    prop="customerName"   label="客户名称" width="150"  > </el-table-column>
                  <el-table-column  prop="contactPersonName"    label="联系人" width="120"  >  </el-table-column>
                  <el-table-column    prop="contactPersonPhone"   label="电话" width="140"  >
                    <template slot-scope="scope">
                      {{scope.row.contactPersonPhone?scope.row.contactPersonPhone:'---'}}
                    </template>
                  </el-table-column>
                  <el-table-column    prop="contactPersonUpdatedDate"   label="上次跟进时间" width="200"  > </el-table-column>
                  <el-table-column  prop="contactPersonStatus"  label="客户状态"   >
                    <template slot-scope="scope">
                      <el-tag type="danger" v-if="scope.row.customerStatusId==='1'">跟进中</el-tag>
                      <el-tag type="success" v-else-if="scope.row.customerStatusId==='2'">已成交</el-tag>
                      <el-tag type="info" v-else>已失效</el-tag>
                    </template>
                  </el-table-column>
                  <!-- <el-table-column   prop="contactPersonPhone"   label="客户电话" width="120" > </el-table-column> -->
                  <!-- 表格操作栏 -->
                  <el-table-column label="操作">
                    <template slot-scope="scope">
                      <el-button type="primary" size="mini" @click="custoerPublicAreaDetail(scope.row.customerId)" plain >详情</el-button>
                      <!-- <el-button type="warning" size="mini" plain >修改</el-button> -->
                    </template>
                  </el-table-column>
                </el-table>
              </el-row>
          <!-- 卡片内容分页区 -->
             <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20, 30, 40]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalNum">
             </el-pagination>
        </el-card>
    </div>
</template>
<script>
import Qs from 'qs'
export default {
  data () {
    return {
      // 客户公海列表
      customerPubliceAreaList: [],
      // 总数据条数
      totalNum: null,
      // 页面数据条数
      pageSize: null,
      // 当前页数
      currentPage: 1
    }
  },
  created () {
    this.getCustomerPublicAreaList()
  },
  methods: {
    // 客户公海详情
    custoerPublicAreaDetail (customerId) {
      this.$router.push({ path: '/detail/custoerPublicAreaDetail', query: { id: customerId } })
    },
    handleCurrentChange (newPage) {
      this.currentPage = newPage
      this.getCustomerPublicAreaList()
    },
    handleSizeChange (newSize) {
      this.pageSize = newSize
      this.getCustomerPublicAreaList()
    },
    async getCustomerPublicAreaList () {
      const data = Qs.stringify({ pageSize: this.pageSize, pageNum: this.currentPage })
      const { data: res } = await this.$http.post('/customer/findCustomerPublicArea', data)
      if (res.status !== 200) return this.$message.error('获取信息失败！')
      this.totalNum = res.data.totalNum - 0
      this.pageSize = res.data.pageSize - 0
      this.currentPage = res.data.currentPage
      this.customerPubliceAreaList = res.data.items
    }
  }

}
</script>

<style lang="less" scoped>

</style>
